h1{
  text-align: center;
}
.App {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  width: 70%;
  grid-gap: 10px 10px;
  min-height: 500px;
  width: 500px;
  margin:auto;
}

.red { 
  background-color:#E0A73F;
  width: 100%;
  height: 100%;
  border-radius: 5%;
  border: none; 
  outline: none;
}

.yellow { 
  transition: opacity 500ms ease-in;
  width: 100%;
  height: 100%;
  border-radius: 5%;
  opacity: 0;
  border: none; 
  outline: none;
}

.blue { 
  background-color:#0C4994;
  width: 100%;
  height: 100%;
  border-radius: 5%;
  border: none; 
  outline: none;
}